<template>
  <article>
    <h1><code>&lt;veui-radiobox&gt;</code></h1>
    <p>
      <veui-radiobox name="a" value="normal" v-model="a1"></veui-radiobox>
    </p>
    <p>
      <veui-radiobox name="a" value="small" ui="small" v-model="a2"></veui-radiobox>
    </p>
    <p>
      <veui-radiobox checked></veui-radiobox>
    </p>
    <p>
      <veui-radiobox ui="small" checked></veui-radiobox>
    </p>
    <p>
      <veui-radiobox checked disabled></veui-radiobox>
    </p>
    <p>
      <veui-radiobox ui="small" checked disabled></veui-radiobox>
    </p>
    <p>
      <veui-radiobox disabled></veui-radiobox>
    </p>
    <p>
      <veui-radiobox ui="small" disabled></veui-radiobox>
    </p>
  </article>
</template>

<script>
import Radiobox from '@/components/Radiobox'

export default {
  name: 'radiobox-demo',
  components: {
    'veui-radiobox': Radiobox
  },
  data () {
    return {
      a1: false,
      a2: false
    }
  }
}
</script>



