<template>
  <article>
    <h1><code>&lt;veui-checkbox&gt;</code></h1>
    <p>
      <veui-checkbox value="normal" v-model="picked1"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox value="small" ui="small" v-model="picked2"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox v-model="picked3"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox ui="small" v-model="picked4"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox indeterminate v-model="picked5"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox ui="small" indeterminate v-model="picked6"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox disabled v-model="picked7"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox ui="small" disabled v-model="picked8"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox disabled v-model="picked9"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox ui="small" disabled v-model="picked10"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox disabled indeterminate v-model="picked11"></veui-checkbox>
    </p>
    <p>
      <veui-checkbox ui="small" disabled indeterminate v-model="picked12"></veui-checkbox>
    </p>
  </article>
</template>

<script>
import Checkbox from '@/components/Checkbox'

export default {
  name: 'checkbox-demo',
  components: {
    'veui-checkbox': Checkbox
  },
  data () {
    return {
      picked1: false,
      picked2: false,
      picked3: true,
      picked4: true,
      picked5: false,
      picked6: false,
      picked7: true,
      picked8: true,
      picked9: false,
      picked10: false,
      picked11: false,
      picked12: false
    }
  }
}
</script>



